                                ></i>
                            </div>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item command=""
                                    ></el-dropdown-item
                                >
                                <el-dropdown-item command=""
                                    ></el-dropdown-item
                                >
                                <el-dropdown-item command=""
                                    ></el-dropdown-item
                                >
                                <el-dropdown-item command=""
                                    ></el-dropdown-item
                                >
                            </el-dropdown-menu>
                        </el-dropdown>
                        <el-dropdown trigger="click" @command="filterTime">
                            <div>
                                <span class="el-dropdown-link">{{
                                    timeType
                                }}</span>
                                <i
                                    class="el-icon-arrow-down el-icon--right"
                                ></i>
                            </div>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item command=""
                                    ></el-dropdown-item
                                >
                                <el-dropdown-item command=""
                                    ></el-dropdown-item
                                >
                            </el-dropdown-menu>
                        </el-dropdown>
                    </div>
                    <div class="button-box">
                        <el-button
                            size="small"
                            :type="usefulButton"
                            :disabled="isFilter"
                            @click="reFilter"
                            ><span class="span"></span></el-button
                        >
                        <el-button
                            size="small"
                            type="new"
                            @click="routerToNewQue"
                            ><span class="span"></span></el-button
                        >
                    </div>
                </div>
            </search-row>
            <el-row class="table-box">
                <el-col :offset="1" :span="22">
                    <el-table
                        stripe
                        :data="tableDataShow"
                        :header-cell-style="{
                            textAlign: 'center',
                            background: '#5F7880',
                        }"
                        :cell-style="{ textAlign: 'center' }"
                        style="width: 100%"
                    >
                        <el-table-column prop="is_favourite" label="">
                            <template slot-scope="scope">
                                <div
                                    @click="
                                        throttleHandleStar(scope.row, scope)
                                    "
                                    style="cursor: pointer"
                                >
                                    <img
                                        style="width: 20px"
                                        v-if="scope.row.is_favourite"
                                        src="../assets/pictures/star2.svg"
                                    />
                                    <img
                                        style="width: 20px"
                                        v-if="!scope.row.is_favourite"
                                        src="../assets/pictures/star1.svg"
                                    />
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column prop="title" label="">
                        </el-table-column>
                        <el-table-column prop="status" label="">
                            <template slot-scope="scope">
                                <el-row>
                                    <el-col
                                        :span="1"
                                        :offset="6"
                                        class="circle-box"
                                    >
                                        <span
                                            :class="
                                                circleStatus(scope.row.status)
                                            "
                                        ></span>
                                    </el-col>
                                    <el-col :span="12">
                                        {{ statusCom(scope.row.status) }}
                                    </el-col>
                                </el-row>
                            </template>
                        </el-table-column>
                        <el-table-column prop="updated_at" label="">
                        </el-table-column>
                        <el-table-column prop="number" label="">
                        </el-table-column>
                        <el-table-column label="">
                            <template slot-scope="scope">
                                <el-button
                                    @click="handleClickEdit(scope.row)"
                                    type="text"
                                    ></el-button
                                >
                                <el-button
                                    type="text"
                                    @click="handleClickAnalyse(scope.row)"
                                    ></el-button
                                >
                            </template>
                        </el-table-column>
                        <el-table-column label="">
                            <template slot-scope="scope">
                                <el-dropdown
                                    @command="handleCommand"
                                    @visible-change="saveRow(scope)"
                                >
                                    <i class="el-icon-more"></i>
                                    <el-dropdown-menu slot="dropdown">
                                        <el-dropdown-item command="publish"
                                            ></el-dropdown-item
                                        >
                                        <el-dropdown-item command="copy"
                                            ></el-dropdown-item
                                        >
                                        <el-dropdown-item command="delete"
                                            ></el-dropdown-item
                                        >
                                    </el-dropdown-menu>
                                </el-dropdown>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
        </el-container>
    </div>
</template>

<script>
import HeaderComponent from "@/components/HeaderComponent.vue";
import SearchRow from "@/components/SearchRow.vue";
import { throttle } from "../utils/throttle.js";
import QRCode from "qrcodejs2";
import {
    myVote,
    addFavorite,
    deleteFavorite,
    deleteQue,
    duplicateQue,
} from "../api/api.js";
export default {
    name: "HomePage",
    components: {
        HeaderComponent,
        SearchRow,
    },
    data() {
        return {
            userName: "",
            tableData: [],
            tableDataShow: [],
            starType: "",
            statusType: "",
            timeType: "",
            usefulButton: "disabled",
            isFilter: true,
            table_row: "table-row",
            inputVal: "",
            searchTempTable: [],
            saveFlag: false,
            rowData: {},
        };
    },
    created() {
        myVote().then((res) => {
            console.log(res);
            this.tableData = res.data.result.map((obj) => obj.vote);
            // console.log(this.tableData)
            this.tableDataShow = this.tableData;
        });
    },
    methods: {
        createQRCode() {
            let qrCodeNode =
                        document.getElementsByClassName("QRcode-box")[0];
                    if (qrCodeNode.childNodes.length > 0) {
                        qrCodeNode.removeChild(qrCodeNode.firstChild);
                        qrCodeNode.removeChild(qrCodeNode.firstChild);
                    }

                    // 
                    let qrcode = new QRCode(qrCodeNode, {
                        width: 200,
                        height: 200,
                    });
                    console.log(qrcode);
                    let elText =
                        document.getElementsByClassName("copy-input-box")[0];
                    qrcode.makeCode(elText);
        },
        saveRow(scope) {
            // console.log(scope)
            this.rowData = scope.row;
            console.log(this.rowData);
        },
        handleCommand(command) {
            const h = this.$createElement;
            const that = this;
            const ques_name = this.rowData["title"];
            const ques_id = this.rowData["vote_id"];
            let copy_title = "";
            // console.log(scope);
            // this.$router.go(0)
            switch (command) {
                case "publish": {
                    console.log(command);
                    this.$msgbox({
                        message: h("div", { class: "publish-box" }, [
                            h("div", { class: "top-box" }, [
                                h(
                                    "input",
                                    {
                                        class: "copy-input-box",
                                        id: "copy-input",
                                        domProps: {
                                            value: "http://localhost:8081/#/homepage",
                                        },
                                    },
                                    []
                                ),
                                h(
                                    "div",
                                    {
                                        class: "copy-button",
                                        on: {
                                            click: () => {
                                                const input =
                                                    document.getElementsByClassName(
                                                        "copy-input-box"
                                                    )[0];
                                                input.select();
                                                console.log(input.value);
                                                if (
                                                    document.execCommand("copy")
                                                ) {
                                                    this.$message.success(
                                                        ""
                                                    );
                                                } else {
                                                    this.$message.warning(
                                                        ""
                                                    );
                                                }
                                            },
                                        },
                                    },
                                    ""
                                ),
                            ]),
                            h("div", { class: "main-box" }, [
                                h("div", { class: "QRcode-box" }),
                                h(
                                    "div",
                                    { class: "QRcode-intro" },
                                    ""
                                ),
                            ]),
                            h("div", { class: "button-box" }, [
                                h("el-button", { class: "el-button--new" }, [
                                    h("span", {}, ""),
                                ]),
                                h("el-button", { class: "el-button--new" }, [
                                    h("span", {}, ""),
                                ]),
                            ]),
                        ]),
                        showConfirmButton: false,
                    });

                    // 
                    if (!document.getElementsByClassName("QRcode-box")[0]) {
                        setTimeout(this.createQRCode, 500);
                    }
                    this.createQRCode();
                    
                    break;
                }
                case "copy":
                    console.log(command);
                    this.$msgbox({
                        message: h("div", { class: "copy-box" }, [
                            h("div", { class: "top-box" }, [
                                h("div", { class: "copy-word" }, ""),
                                h(
                                    "input",
                                    {
                                        domProps: {
                                            value: copy_title,
                                        },
                                        class: "copy-input-box",
                                        on: {
                                            input: function (e) {
                                                copy_title = e;
                                                console.log(e);
                                            },
                                        },
                                    },
                                    []
                                ),
                            ]),
                            h("div", { class: "main-box" }, [
                                h("div", { class: "word" }, ""),
                                h("el-checkbox", {}),
                            ]),
                            h("div", { class: "button-box" }, [
                                h("el-button", { class: "el-button--new" }, [
                                    h(
                                        "span",
                                        {
                                            on: {
                                                click: function () {
                                                    let duplicate_obj = {
                                                        voteId: ques_id,
                                                    };
                                                    console.log("helloworld");
                                                    duplicateQue(
                                                        duplicate_obj
                                                    ).then((res) => {
                                                        console.log(res);
                                                        that.$message({
                                                            message:
                                                                "!",
                                                            type: "success",
                                                        });
                                                        setTimeout(() => {
                                                            that.$router.go(0);
                                                        }, 500);
                                                    });
                                                },
                                            },
                                        },
                                        ""
                                    ),
                                ]),
                                h("el-button", { class: "el-button--cancel" }, [
                                    h(
                                        "span",
                                        {
                                            on: {
                                                click: () => {
                                                    this.$msgbox.close();
                                                },
                                            },
                                        },
                                        ""
                                    ),
                                ]),
                            ]),
                        ]),
                        showConfirmButton: false,
                    });
                    break;
                case "delete":
                    console.log(command);
                    console.log("hello,world");
                    this.$msgbox({
                        message: h("div", { class: "msgbox-box" }, [
                            h(
                                "span",
                                { class: "msgbox-word" },
                                `${ques_name}?`
                            ),
                            // h('div', {class: 'button-box'}, [
                            //     h('el-button', {type: 'danger', click: 'try'}, ''),
                            //     h('el-button', {}, ''),
                            // ])
                        ]),
                        // showConfirmButton: false,
                        showCancelButton: true,
                        confirmButtonText: "",
                        cancelButtonText: "",
                        customClass: "msgbox-delete",
                        cancelButtonClass: "cancel-button-1",
                        confirmButtonClass: "confirm-button-1",
                        beforeClose: (action, instance, done) => {
                            if (action == "confirm") {
                                instance.confirmButtonLoad = true;
                                instance.confirmButtonText = "";
                                // 
                                let del_obj = {
                                    voteId: ques_id,
                                };
                                console.log(ques_id);
                                deleteQue(del_obj).then((res) => {
                                    console.log(res);
                                    that.$message({
                                        message: "!",
                                        type: "success",
                                    });
                                    setTimeout(() => {
                                        that.$router.go(0);
                                    }, 500);
                                });
                            } else {
                                done();
                            }
                        },
                        center: true,
                    });
                    break;
                default:
                    console.log(command);
                    // console.log(scope)
                    break;
            }
        },
        handleClickEdit(row) {
            console.log(row);
            this.$router.push("/edit");
        },
        handleClickAnalyse(row) {
            console.log(row);
            this.$router.push(`/analyse/${row.vote_id}/result`);
        },
        filterStar(command) {
            this.starType = command;
        },
        filterStatus(command) {
            this.statusType = command;
        },
        filterTime(command) {
            this.timeType = command;
        },
        reFilter() {
            this.starType = "";
            this.statusType = "";
            this.timeType = "";
            this.tableDataShow = this.tableData;
        },
        filterChange(value) {
            function filterValue(obj) {
                if (obj.name.includes(value)) return true;
                else return false;
            }
            if (value) {
                if (this.saveFlag == false) {
                    this.searchTempTable = this.tableDataShow;
                    this.saveFlag = true;
                }
                this.tableDataShow = this.tableDataShow.filter(filterValue);
            } else {
                this.saveFlag = false;
                this.tableDataShow = this.searchTempTable;
            }
        },
        routerToNewQue() {
            this.$store.commit('emitQues', {});
            this.$router.push("/edit");
        },
        // 
        handleStar(row, scope) {
            let obj = {};
            // 
            obj.type = 0;
            obj.id = row.vote_id;
            console.log(scope);
            // 
            if (row.is_favourite === 0) {
                addFavorite(obj).then((res) => {
                    console.log(res);
                    this.$set(
                        this.tableDataShow[scope.$index],
                        "is_favourite",
                        !this.tableDataShow[scope.$index].is_favourite
                    );
                });
            } else {
                deleteFavorite(obj).then((res) => {
                    console.log(res);
                    this.$set(
                        this.tableDataShow[scope.$index],
                        "is_favourite",
                        !this.tableDataShow[scope.$index].is_favourite
                    );
                });
            }
        },
        //   
        throttleHandleStar(row, scope) {
            throttle(this.handleStar(row, scope), 500);
        },
    },
    watch: {
        starType: function (newValue) {
            if (
                newValue == "" &&
                this.statusType == "" &&
                this.timeType == ""
            ) {
                this.isFilter = true;
                this.usefulButton = "disabled";
            } else {
                this.isFilter = false;
                this.usefulButton = "new";
            }

            let statusType = this.statusType;
            function checkStar(obj) {
                if (statusType == "") return obj.is_favourite;
                else if (statusType == "") {
                    return obj.is_favourite && obj.status == 0;
                } else if (statusType == "") {
                    return obj.is_favourite && obj.status == 1;
                }
            }

            function checkNoStar(obj) {
                if (statusType == "") return !obj.is_favourite;
                else if (statusType == "") {
                    return !obj.is_favourite && obj.status == 0;
                } else if (statusType == "") {
                    return !obj.is_favourite && obj.status == 1;
                }
            }

            if (newValue == "")
                this.tableDataShow = this.tableData.filter(checkStar);
            else if (newValue == "") {
                this.tableDataShow = this.tableData.filter(checkNoStar);
            } else if (newValue == "") {
                this.tableDataShow = this.tableData;
            }
        },
        statusType: function (newValue) {
            if (
                newValue == "" &&
                this.starType == "" &&
                this.timeType == ""
            ) {
                this.isFilter = true;
                this.usefulButton = "disabled";
            } else {
                this.isFilter = false;
                this.usefulButton = "new";
            }

            let starType = this.starType;
            function checkStateGoing(obj) {
                if (starType == "") return obj.status == 1;
                else if (starType == "")
                    return obj.status == 1 && obj.is_favourite;
                else if (starType == "")
                    return obj.status == 1 && !obj.is_favourite;
            }
            function checkStatePause(obj) {
                if (starType == "") return obj.status == 0;
                else if (starType == "")
                    return obj.status == 0 && obj.is_favourite;
                else if (starType == "")
                    return obj.status == 0 && !obj.is_favourite;
            }

            if (newValue == "") {
                this.tableDataShow = this.tableData.filter(checkStateGoing);
            } else if (newValue == "") {
                this.tableDataShow = this.tableData.filter(checkStatePause);
            }
        },
        timeType: function (newValue) {
            if (
                newValue == "" &&
                this.starType == "" &&
                this.statusType == ""
            ) {
                this.isFilter = true;
                this.usefulButton = "disabled";
            } else {
                this.isFilter = false;
                this.usefulButton = "new";
            }

            function MsgSortNow(arr) {
                arr.sort((a, b) => {
                    let t1 = new Date(Date.parse(a.updated_at));
                    let t2 = new Date(Date.parse(b.updated_at));

                    return t2.getTime() - t1.getTime();
                });
                console.log(arr);
                return arr;
            }

            function MsgSortRev(arr) {
                arr.sort((a, b) => {
                    let t1 = new Date(Date.parse(b.updated_at));
                    let t2 = new Date(Date.parse(a.updated_at));

                    return t2.getTime() - t1.getTime();
                });
                console.log(arr);
                return arr;
            }
            console.log(newValue);

            if (newValue == "") {
                this.tableDataShow = MsgSortNow(this.tableDataShow);
            } else if (newValue == "") {
                this.tableDataShow = MsgSortRev(this.tableDataShow);
            } else if (newValue) {
                console.log("fefwsfe");
                this.tableDataShow = this.tableData;
            }

            console.log(this.isFilter);
        },
    },
    computed: {
        circleStatus: function () {
            return function (status) {
                let isGoing = status == 1;
                let isPause = status == 0;
                return {
                    circle: true,
                    going: isGoing,
                    pause: isPause,
                };
            };
        },
        statusCom: function () {
            return function (status) {
                if (status == 0) {
                    return "";
                } else if (status == 1) {
                    return "";
                }
            };
        },
    },
};
</script>

